      if (data.franchiseTypes.selfCarWash) selectedTypes.push("ÏÖÄÌîÑÏÑ∏Ï∞®Ïû•");
      if (data.franchiseTypes.noBrush) selectedTypes.push("ÎÖ∏Î∏åÎü¨Ïâ¨");
